* {
  margin: 0;
  padding: 0;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #333;
  color: #fff;
}
.game-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

  flex-direction: column;
}
.cell-content {
  padding: 25px 15px;
  color: #333;
  border: 1px solid #333;
  text-align: center;
  cursor: pointer;
  position: relative;
  background-color: #fff;
}
.cell-content::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 0;
  background-color: #fff;
  transition: all 0.3s ease-in-out;
  z-index: -1;
}
.cell-content {
  z-index: 1;
}
.play-again {
  margin-bottom: 1rem;
}
.text-uppecase {
  text-transform: uppercase;
}
.cell-content.active-class::before {
  background-color: red;
  height: 100%;
}
.cell-content.green-class::before {
  background-color: green;
  height: 100%;
}
.cell-content.active-class,
.cell-content.green-class {
  color: #fff;
}
